import React, {useState} from "react"
import {Link} from "gatsby"
import DarkMode from "../components/DarkMode"
import Footer from "../components/Footer"
import { useSpring, animated } from 'react-spring'
import Rick from "../images/rick.png"

export default function Home() {
  const [animateDiv,setAnimateDiv]=useState(true);
  const styles = useSpring({ opacity: animateDiv ? 1 : 0 })
  const rickStyles = useSpring({left:animateDiv?"-150px":"0px"})

  const animatePage =() =>{
    setAnimateDiv(false);
    setTimeout(
      () => setAnimateDiv(true), 
      2000
    );
  }

  return (
    <>
      <DarkMode/>
      <animated.img style={rickStyles} src={Rick} className="rick-img"/>
      <div className="samrats-container">
        <h1>Samrat Pandey</h1>
        <h3>Frontend Web Developer</h3>

        <ul>
          <li>&lt;html:5&gt;</li>
          <li><sup>*</sup>{"{"}css{"}"}</li>
          <animated.li style={styles} className="animate" onClick={()=>animatePage()}>Animation</animated.li>
          <li>javaScript();</li>
          <li>React.Component{"{}"}</li>
          <li>git</li>
        </ul>

        <div className="samrat-contact">
          <Link to="/myWorks">view my works&#8640;</Link>
          <Link to="/contactMe">get in touch&#8640;</Link>
        </div>

        <Footer/>
      </div>
    </>
  )
}
